<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 更新运行中的交易对参数
            v-container
                v-row 
                    v-col(cols="12" sm="6" lg="4")
                        span 策略:{{filename}}
                    v-col(cols="12" xs="6" sm="6" lg="4")
                        span 已买金额:{{buymoney}}
                    v-col(cols="12" xs="6" sm="6" lg="4")
                        span 已买数量:{{buyamount}}
                    v-col(cols="12" xs="6" sm="6" lg="4")
                        span 买入均价:{{buyavgprice}}
                    v-col(cols="12" xs="6" sm="6" lg="4")
                        span 当前价值:{{currencyasset}}
                    v-col(cols="12" xs="6" sm="6" lg="4")
                        span 当前盈利:{{currencyprofit}}

                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="sepminutes" label="下单周期(分钟)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="onehandmoney" label="买入金额(元)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="maxmoneyonecycle" label="最大买入金额(元)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details disabled v-model="maxdownratio" label="最大下跌幅度")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="sepseconds" label="刷新间隔(秒)")
                    
            v-card-actions
                v-btn(color="success" dark @click="update") 更新
</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    sepminutes:1440,
    onehandmoney:15,
    maxmoneyonecycle:50,
    
    sepseconds:5,
    ExName:"WKJ",
    }),
    computed: {
    upexconfig:get('exbot/updtmconfig'),
    maxdownratio(){
        return this.upexconfig.maxdownratio
    },
    tarCoin(){
        return this.upexconfig.tarcoin
    },
    baseCoin(){
        return this.upexconfig.basecoin
    },
    buyamount(){
        return this.upexconfig.buyamount
    },
    buymoney(){
        return this.upexconfig.buymoney
    },
    buyavgprice(){
        return this.upexconfig.buyavgprice
    },
    currencyasset(){
        return this.upexconfig.currencyasset
    },
    currencyprofit(){
        return this.upexconfig.currencyprofit
    },
    filename(){
        return this.upexconfig.filename
    },
    email:get('user/email'),
    isLogin:get('user/islogin'),
    username:get('user/username'),
    exbotisexpire:get('user/exbotisexpire'),
    },
    methods:{
        update(){
            if (!this.isLogin){
                this.$store.dispatch("msglog/push_msg",{message:"用户未登陆，请登陆后重试",type:"error"});
                return
            }
            if (this.exbotisexpire){
                this.$store.dispatch("msglog/push_msg",{message:"软件已过期，请联系管理员",type:"error"});
                return
            }

            let params = {
                "username": this.username,
                "exname": this.ExName,
                "tarcoin": this.tarCoin,
                "basecoin": this.baseCoin,
                "sepminutes": Number(this.sepminutes),
                "onehandmoney":Number(this.onehandmoney),
                "maxmoneyonecycle": Number(this.maxmoneyonecycle),
                "maxdownratio": Number(this.maxdownratio),
                "notifyemail": this.email,
                "sepseconds": Number(this.sepseconds),
                "filename":this.filename,
            }
            api.DTMPutOne(params).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    }
}
</script>